  padding: 90px 0;
}
.methodology-wrapper {
  display: flex;
  flex-direction: column;
  gap: 60px;
  position: relative;
}
.methodology-wrapper .methodology-backround-img {
  width: 553px;
  height: 529px;
  position: absolute;
  top: 200px;
  left: -200px;
}
.methodology-wrapper .methodology-backround-img-tactic {
  width: 553px;
  height: 529px;
  position: absolute;
  top: 200px;
  right: -200px;
  z-index: 1;
}

.guide {
  padding: 79px 0 85px 0;
}
.guide-wrapper {
  display: flex;
  flex-direction: column;
  gap: 45px;
}
.guide-wrapper .guide-top-content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.guide-wrapper .guide-top-content .guide-top-btn {
  padding: 6px 28px;
  background-color: var(--dark-blue);
  border-radius: 105px;
  display: flex;
  gap: 22px;
}
.guide-wrapper .guide-top-content .guide-top-btn-text {
  font-family: "Dela Gothic One";
  font-size: 22px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 51px;
}

.question {
  background-color: var(--dark-blue);
  padding: 88px 0;
}
.question-wrapper {
  display: flex;
  flex-direction: column;
  gap: 70px;
}
.question .accordion .accordion-item {
  position: relative;
  padding: 18px 0;
}
.question .accordion .accordion-item::before {
  content: "";
  height: 1px;
  width: 100%;
  background-color: var(--white);
  position: absolute;
  top: 0;
}
.question .accordion .accordion-item:last-child::after {
  content: "";
  height: 1px;
  width: 100%;
  background-color: var(--white);
  position: absolute;
  bottom: 0;
}
.question .accordion .accordion-item-title {
  font-size: 24px;
  font-weight: 600;
  line-height: 32px;
  position: relative;
  margin-left: 50px;
  cursor: pointer;
}
.question .accordion .accordion-item-title::before {
  content: "";
  width: 19px;
  height: 19px;
  background-size: cover;
  position: absolute;
  left: -35px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("../images/icons/plus.svg");
  transition: 0.5s;
  transform-origin: center;
}
.question .accordion .accordion-content {
  margin-left: 50px;
  height: 0;
  overflow: hidden;
  transition: 0.5s;
  overflow-y: auto;
}
.question .accordion .accordion-content-text {
  font-size: 24px;
  font-weight: 700;
  line-height: 33px;
}
.question .accordion .accordion-item.accordion-active .accordion-content {
  height: fit-content;
  padding: 32px 0;
}
.question .accordion .accordion-item.accordion-active .accordion-item-title::before {
  transform: translateY(-50%) rotate(45deg);
}

.container {
  max-width: 1462px;
  margin: 0 auto;
}

.small__container {
  max-width: 1240px;
  margin: 0 auto;
}

.title__wrapper {
  width: fit-content;
}
.title__wrapper .title {
  text-transform: uppercase;
  font-weight: 400;
  font-size: 48px;
  font-family: "Dela Gothic One";
  color: var(--white);
  padding: 5px 30px;
  border-radius: 105px;
  background-color: var(--dark-blue);
  width: fit-content;
}
.title__wrapper .title__light {
  background-color: var(--light-blue);
  color: var(--dark-blue);
}
.title__wrapper .under__title-wrapper {
  display: flex;
  justify-content: center;
  margin-left: 50px;
}
.title__wrapper .under__title-wrapper-form {
  margin: 0;
}
.title__wrapper .under__title-wrapper .under__title {
  color: var(--dark-blue);
  padding: 5px 30px;
  border-radius: 105px;
  font-size: 32px;
  background-color: var(--white);
  width: fit-content;
  transform: rotate(-5deg);
  border: 4px solid var(--light-blue);
}
.title__wrapper .under__title-wrapper .under__title__light {
  color: var(--white);
  background-color: var(--dark-blue);
  border: 4px solid var(--white);
}
.title__wrapper .under__title-wrapper .under__title-border {
  border: none;
}
.title__wrapper .under__title-wrapper .under__title__form {
  background-color: var(--light-blue);
  border: 4px solid var(--white);
  font-size: 30px;
  white-space: nowrap;
}

.btn__link {
  border: none;
  background-color: var(--light-blue);
  padding: 19px 96px;
  color: var(--dark-blue);
  font-size: 16px;
  font-weight: 700;
  border-radius: 30px;
  position: relative;
  text-transform: uppercase;
}
.btn__link-form {
  margin-top: 55px;
  background-color: var(--dark-blue);
  color: var(--white);
}
.btn__link-dark {
  background-color: var(--dark-blue);
  color: var(--white);
}
.btn__link::after {
  content: "";
  position: absolute;
  height: 48px;
  width: 48px;
  background-image: url("../images/icons/black-arrow.svg");
  background-position: center;
  background-size: 12px;
  background-repeat: no-repeat;
  background-color: var(--white);
  border-radius: 50%;
  right: 7px;
  top: 50%;
  transform: translateY(-50%);
}

.box__content__wrapper {
  display: flex;
  gap: 50px;
}
.box__content__wrapper .box__content__left {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.box__content__wrapper .box__content__left .box__small__item {
  position: relative;
  background: var(--white);
  padding: 35px 40px 30px 240px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  color: var(--black);
  border-radius: 44px;
  z-index: 1;
  width: 574px;
}
.box__content__wrapper .box__content__left .box__small__item::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 30%;
  background-color: var(--dark-blue);
  border-radius: 44px;
  top: -10px;
  left: 0;
  transform: rotate(-2deg);
  z-index: -1;
}
.box__content__wrapper .box__content__left .box__small__item::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--white);
  border-radius: 44px;
  top: 0;
  left: 0;
  z-index: -1;
}
.box__content__wrapper .box__content__left .box__small__item__img {
  width: 269px;
  height: 269px;
  position: absolute;
  left: 0;
  bottom: 0;
}
.box__content__wrapper .box__content__right {
  display: flex;
  flex-direction: column;
  gap: 50px;
  width: 100%;
}
.box__content__wrapper .box__content__right .box__big__item {
  padding: 357px 33px 45px 56px;
  background: var(--white);
  display: flex;
  flex-direction: column;
  gap: 15px;
  color: var(--black);
  border-radius: 44px;
  height: 100%;
  justify-content: flex-end;
  position: relative;
  z-index: 2;
}
.box__content__wrapper .box__content__right .box__big__item::before {
  content: "";
  position: absolute;
  width: 90%;
  height: 30%;
  background-color: var(--dark-blue);
  border-radius: 44px;
  top: -20px;
  left: 0;
  transform: rotate(-2deg);
  z-index: -1;
}
.box__content__wrapper .box__content__right .box__big__item::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--white);
  border-radius: 44px;
  top: 0;
  left: 0;
  z-index: -1;
}
.box__content__wrapper .box__content__right .box__big__item__img {
  position: absolute;
  top: -70px;
}
.box__content__wrapper .box__item__title {
  font-size: 25px;
  font-weight: 700;
  line-height: 31px;
  text-transform: uppercase;
}
.box__content__wrapper .box__item__text {
  font-size: 18px;
  font-weight: 400;
  line-height: 25px;
}

.coach__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 17px;
}
.coach__wrapper .coach__card {
  background-color: var(--light-blue);
  display: flex;
  flex-direction: column;
  border-radius: 35px;
  overflow: hidden;
}
.coach__wrapper .coach__card__img__wrapper {
  height: 373px;
}
.coach__wrapper .coach__card__bottom__content {
  display: flex;
  align-items: flex-end;
}
.coach__wrapper .coach__card__bottom__content .coach__card__number__wrapper {
  padding: 30px 40px 30px 40px;
  border-top-right-radius: 42px;
  border-bottom-left-radius: 42px;
  border-bottom-right-radius: 42px;
  background-color: var(--light-blue);
  position: relative;
  z-index: 0;
}
.coach__wrapper .coach__card__bottom__content .coach__card__number__wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--white);
  bottom: 0;
  left: 0;
  border-top-right-radius: 42px;
  z-index: -1;
}
.coach__wrapper .coach__card__bottom__content .coach__card__number__wrapper::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--light-blue);
  bottom: 0;
  left: 0;
  border-top-right-radius: 42px;
  border-bottom-right-radius: 42px;
  z-index: -1;
}
.coach__wrapper .coach__card__bottom__content .coach__card__number__wrapper .coach__card__number__text {
  font-size: 42px;
  font-weight: 700;
  color: var(--dark-blue);
  line-height: 28px;
}
.coach__wrapper .coach__card__bottom__content .coach__card__name__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 51px;
  background-color: var(--dark-blue);
  width: 100%;
  border-radius: 37px;
  justify-content: center;
  height: fit-content;
  position: relative;
  z-index: 0;
}
.coach__wrapper .coach__card__bottom__content .coach__card__name__wrapper .coach__card__name {
  font-size: 20px;
  font-weight: 700;
  line-height: 16px;
}
.coach__wrapper .coach__card__bottom__content .coach__card__name__wrapper .coach__card__patronymic {
  font-size: 36px;
  font-weight: 700;
  line-height: 30px;
  max-width: 200px;
}
.coach__wrapper .coach__card__bottom__content .coach__card__name__wrapper::before {
  content: "";
  height: 120%;
  width: 100%;
  position: absolute;
  background-color: var(--white);
  z-index: -1;
  border-top-left-radius: 37px;
  border-top-right-radius: 37px;
}
.coach__wrapper .coach__card__bottom__content .coach__card__name__wrapper::after {
  content: "";
  height: 100%;
  width: 95%;
  position: absolute;
  background-color: var(--dark-blue);
  z-index: -1;
  border-radius: 37px;
}

.form {
  padding: 75px 0 125px 0;
}
.form__wrapper {
  display: flex;
  gap: 23px;
}
.form__wrapper .form__left__content {
  width: 49.5161290323%;
  position: relative;
}
.form__wrapper .form__left__content-img {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 100px;
}
.form__wrapper .form__left__content-title-wrapper {
  padding: 28px 173px 30px 50px;
  background-color: var(--dark-blue);
  border-radius: 35px;
}
.form__wrapper .form__left__content-title-wrapper .form__left__content-title {
  font-family: "Dela Gothic One";
  font-size: 36px;
  font-weight: 400;
  line-height: 43px;
}
.form__wrapper .form__right__content {
  width: 48.3870967742%;
  padding: 51px 60px 55px 60px;
  background-color: var(--light-blue);
  border-radius: 44px;
  display: flex;
  flex-direction: column;
  gap: 45px;
}
.form__wrapper .form__right__content .form__content {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.form__wrapper .form__right__content .form__content .form__input {
  border: none;
  outline: none;
  padding: 19px;
  border-radius: 17px;
  font-size: 17px;
  line-height: 20px;
  font-weight: 500;
}
.form__wrapper .form__right__content .form__content .real-checkbox {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
  z-index: -1;
}
.form__wrapper .form__right__content .form__content .custom-checkbox {
  display: inline-block;
  width: 17px;
  height: 17px;
  border: 2px solid var(--dark-blue);
  vertical-align: sub;
  margin-right: 5px;
  position: relative;
}
.form__wrapper .form__right__content .form__content .custom-checkbox::before {
  content: "";
  display: inline-block;
  width: 11px;
  height: 11px;
  background-image: url("../images/icons/mark.svg");
  background-size: contain;
  background-repeat: no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  margin-top: 1px;
  margin-left: 1px;
  transition: 0.2s ease-in;
}
.form__wrapper .form__right__content .form__content .real-checkbox:checked + .custom-checkbox::before {
  transform: translate(-50%, -50%) scale(1);
}
.form__wrapper .form__right__content .form__content .real-checkbox:checked + .custom-checkbox {
  background-color: var(--dark-blue);
}
.form__wrapper .form__right__content .form__content .form__text {
  font-size: 11px;
  font-weight: 400;
  color: var(--dark-blue);
}